import React, { useEffect, useState } from 'react'
import { getProduct } from '../../Redux/ActionCreator/ProductActionCreator'
import { getMaincategory } from '../../Redux/ActionCreator/MaincategoryActionCreator'
import { getSubcategory } from '../../Redux/ActionCreator/SubcategoryActionCreator'
import { getBrand } from '../../Redux/ActionCreator/BrandActionCreator'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useLocation } from 'react-router-dom'
import '../../assets/custom.css'

export default function ShopContent() {
    let [products, setProducts] = useState([])
    let [maincategory, setMaincategory] = useState([])
    let [subcategory, setSubcategory] = useState([])
    let [brand, setBrand] = useState([])
    let [mc, setMc] = useState("All")
    let [sc, setSc] = useState("All")
    let [br, setBr] = useState("All")
    let [flag, setFlag] = useState(false)
    let [min, setMin] = useState(0)
    let [max, setMax] = useState(0)
    let [search, setSearch] = useState()
    let location = useLocation()
    let dispatch = useDispatch()
    let ProductStateData = useSelector(state => state.ProductStateData)
    let MaincategoryStateData = useSelector(state => state.MaincategoryStateData)
    let SubcategoryStateData = useSelector(state => state.SubcategoryStateData)
    let BrandStateData = useSelector(state => state.BrandStateData)

    function filterData(mc, sc, br, min = -1, max = -1) {
        let data = []
        if (mc === "All" && sc === "All" && br === "All")
            data = ProductStateData
        else if (mc !== "All" && sc === "All" && br === "All")
            data = ProductStateData.filter(x => x.maincategory?.name === mc)
        else if (mc === "All" && sc !== "All" && br === "All")
            data = ProductStateData.filter(x => x.subcategory?.name === sc)
        else if (mc === "All" && sc === "All" && br !== "All")
            data = ProductStateData.filter(x => x.brand?.name === br)
        else if (mc !== "All" && sc !== "All" && br === "All")
            data = ProductStateData.filter(x => x.maincategory?.name === mc && x.subcategory?.name === sc)
        else if (mc !== "All" && sc === "All" && br !== "All")
            data = ProductStateData.filter(x => x.maincategory?.name === mc && x.brand?.name === br)
        else if (mc === "All" && sc !== "All" && br !== "All")
            data = ProductStateData.filter(x => x.subcategory?.name === sc && x.brand?.name === br)
        else
            data = ProductStateData.filter(x => x.subcategory?.name === sc && x.brand?.name === br && x.maincategory?.name === mc)

        if (min !== -1 && max !== -1)
            data = data.filter((x) => x.finalPrice >= min && x.finalPrice <= max)
        setProducts(data)
    }

    function sortFilter(option) {
        if (option === "1")
            setProducts(products.sort((x, y) => y._id.localeCompare(x._id)))
        else if (option === "2")
            setProducts(products.sort((x, y) => y.finalPrice - x.finalPrice))
        else
            setProducts(products.sort((x, y) => x.finalPrice - y.finalPrice))
        setFlag(!flag)
    }

    function priceFilter(e) {
        e.preventDefault()
        filterData(mc, sc, br, min, max)
    }

    function postSearch(e) {
        e.preventDefault()
        let ch = search.toLowerCase()
        setProducts(ProductStateData.filter((x) => x.name.toLowerCase().includes(ch) || x.maincategory?.name.toLowerCase() === ch || x.subcategory?.name.toLowerCase() === ch || x.brand?.name.toLowerCase() === ch || x.color.toLowerCase() === ch || x.descripton?.toLowerCase().includes(ch)))
    }

    useEffect(() => {
        (() => {
            dispatch(getProduct())
        })()
    }, [ProductStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getMaincategory())
            if (MaincategoryStateData.length)
                setMaincategory(MaincategoryStateData)
        })()
    }, [MaincategoryStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getSubcategory())
            if (SubcategoryStateData.length)
                setSubcategory(SubcategoryStateData)
        })()
    }, [SubcategoryStateData.length])

    useEffect(() => {
        (() => {
            dispatch(getBrand())
            if (BrandStateData.length)
                setBrand(BrandStateData)
        })()
    }, [BrandStateData.length])

    useEffect(() => {
        let query = new URLSearchParams(location.search)
        setMc(query.get("mc") ?? "All")
        setSc(query.get("sc") ?? "All")
        setBr(query.get("br") ?? "All")
        filterData(query.get("mc") ?? "All", query.get("sc") ?? "All", query.get("br") ?? "All")
    }, [location, ProductStateData.length])

    return (
        <>
            <div className="container-fluid fruite mt-1 mt-md-4 mt-lg-4">
                <div className="row g-2">
                    <div className="col-lg-12">
                        <div className="row g-2">
                            <div className="col-xl-3">
                                <h3 className="mb-4 d-none d-md-block d-lg-block">PRODUCT SHOP</h3>
                            </div>
                            <div className="col-xl-3">
                                <form onSubmit={postSearch}>
                                    <div className="input-group w-100 mx-auto d-flex">
                                        <input type="search" name='search' onChange={(e) => setSearch(e.target.value)} className="form-control p-2" placeholder="keywords" aria-describedby="search-icon-1" />
                                        <button type='submit' id="search-icon-1" className="input-group-text"><i className="fa fa-search"></i></button>
                                    </div>
                                </form>
                            </div>
                            <div className="col-xl-3">
                                <div className="bg-white ps-3 pt-2 pb-1 rounded d-flex justify-content-between">
                                    <label htmlFor="fruits">Default Sorting :</label>
                                    <select id="fruits" name="sort" onChange={(e) => sortFilter(e.target.value)} className="border-0 form-select-sm me-3 rounded">
                                        <option value="1">Latest</option>
                                        <option value="2">High to Low</option>
                                        <option value="3">Low to High</option>
                                    </select>
                                </div>
                            </div>
                            <div className="col-xl-3">
                                <div className="dropdown">
                                    <button className="bg-white text-dark ps-3 py-2 border-0  rounded mb-2 w-100 dropdown-toggle" type="button" data-bs-toggle="dropdown" aria-expanded="false">
                                        Category and Product Filter
                                    </button>
                                    <ul className="dropdown-menu w-100 category_dropdown">
                                        <h4 className='ms-3'>Maincategories</h4>
                                        <li>
                                            <div className="d-flex justify-content-between fruite-name">
                                                <Link to={`/shop?mc=All&sc=${sc}&br=${br}`} className='text-info dropdown-item'>All</Link>
                                            </div>
                                        </li>
                                        {
                                            maincategory.map((item, index) => {
                                                return <li key={index}>
                                                    <div className="d-flex justify-content-between fruite-name">
                                                        <Link to={`/shop?mc=${item.name}&sc=${sc}&br=${br}`} className='text-info dropdown-item' >{item.name}</Link>
                                                    </div>
                                                </li>
                                            })
                                        }
                                        <hr />
                                        <h4 className='ms-3'>Subcategories</h4>
                                        <li>
                                            <div className="d-flex justify-content-between fruite-name">
                                                <Link to={`/shop?mc=${mc}&sc=All&br=${br}`} className='text-info dropdown-item'>All</Link>
                                            </div>
                                        </li>
                                        {
                                            subcategory.map((item, index) => {
                                                return <li key={index}>
                                                    <div className="d-flex justify-content-between fruite-name">
                                                        <Link to={`/shop?mc=${mc}&sc=${item.name}&br=${br}`} className='text-info dropdown-item' >{item.name}</Link>
                                                    </div>
                                                </li>
                                            })
                                        }
                                        <hr />
                                        <h4 className='ms-3'>Brand</h4>
                                        <li>
                                            <div className="d-flex justify-content-between fruite-name">
                                                <Link to={`/shop?mc=${mc}&sc=${sc}&br=All`} className='text-info dropdown-item'>All</Link>
                                            </div>
                                        </li>
                                        {
                                            brand.map((item, index) => {
                                                return <li key={index}>
                                                    <div className="d-flex justify-content-between fruite-name">
                                                        <Link to={`/shop?mc=${mc}&sc=${sc}&br=${item.name}`} className='text-info dropdown-item' >{item.name}</Link>
                                                    </div>
                                                </li>
                                            })
                                        }
                                        <hr />
                                        <div className='mx-2'>
                                            <p className='bg-info text-center p-2 text-dark'>Price Filter</p>
                                            <form onSubmit={priceFilter}>
                                                <div className='d-flex gap-4 mb-3'>
                                                    <input type="number" name="min" onChange={(e) => setMin(e.target.value)} className='form-control w-50' placeholder='Min Amount' />
                                                    <input type="number" name="max" onChange={(e) => setMax(e.target.value)} className='form-control w-50' placeholder='Max Amount' />
                                                </div>
                                                <button type='submit' className='btn btn-info w-100'>Filter</button>
                                            </form>
                                        </div>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <hr className='mt-0' />
                        <div className="row g-4">
                            <div className="col-lg-12">
                                <div className="row gx-2 gy-2 justify-content-center">
                                    {
                                        products.map((item, index) => {
                                            return <div key={index} className="col-6 col-md-4 col-lg-3 col-xl-2">
                                                <Link to={`/product/${item._id}`} >
                                                    <div className="position-relative fruite-item border bg-white">
                                                        <div>
                                                            <img src={`/${item.pic[0]}`} className="img-fluid w-100 category-image" alt={item.name} />
                                                        </div>
                                                        <div className="text-white bg-dark px-2 rounded position-absolute" style={{ top: "10px", left: "10px", fontSize: "10px" }}>{item.brand.name}</div>
                                                        <div className="py-2">
                                                            <Link to={`/product/${item._id}`}><h6 className='px-2'>{item.name.slice(0, 25)}</h6></Link>
                                                            <div className="d-flex flex-lg-wrap px-2">
                                                                <span>&#8377;{item.finalPrice} <sup>{item.discount}% off</sup></span>
                                                            </div>

                                                        </div>
                                                    </div>
                                                </Link>
                                            </div>
                                        })
                                    }
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}






















